<template>
  <div>
    <el-card style="max-width: 960px;margin: 0 auto;">
      <div slot="header">
        <span></span>
      </div>
      <el-form
        label-position="left"
        label-width="100px"
        :model="formData"
        :rules="rules"
        ref="createForm"
        @submit.native.prevent="submit('createForm')"
      >
        <el-form-item label="" prop="title">
          <el-input v-model="formData.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="type">
          {{formData.categoryId}}
          <el-select v-model="formData.categoryId" placeholder="">
            <el-option
              v-for="item in typeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input v-model="formData.description"></el-input>
        </el-form-item>
        <el-form-item label="RSS Feed" prop="title">
          <el-input v-model="formData.feed"></el-input>
        </el-form-item>
        <el-form-item label="" prop="title">
          <div class="avatar-img">
            <input
            id="avatar"
            type="file"
            accept="image/png,image/jpg, image/ico"
            @change="upload"
            style="display:none;"
          />
          <el-button type="primary" @click="toUpload"></el-button>
          </div>
          <img v-if="image" :src="image" width="100" height="100" />
        </el-form-item>
        <el-form-item class="text-center">
          <el-button type="primary" native-type="submit"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <img src alt srcset />
  </div>
</template>
<script>
import { toUrlParams } from "@/common/js/util";
import axios from "axios";
export default {
  name: "CreateMediaList",
  data() {
    return {
      formData: {},
      rules: {
        title: [
          {
            required: false,
            message: "",
            trigger: "blur"
          }
        ],
        categoryId: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        feed: [
          {
            required: true,
            message: "RSS Feed",
            trigger: "blur"
          }
        ],
        file: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ]
      },
      options: [],
      typeOptions: [],
      value: "",
      image: "",
      sign: {},
      fileList: []
    };
  },
  created() {
    this.queryTypes();
    if (this.$route.query.id) {
      this.queryDetail(this.$route.query.id);
    }
  },
  methods: {
    show(){
      console.log(e);
    },
    initFormData() {
      this.formData = {
        name: "",
        category: "",
        categoryId: "",
        description: "",
        feed: "",
        icon: ""
      };
    },
    // 
    submit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          // 
          this.save();
          return true;
        }
        return false;
      });
    },
    save() {
      this.formData.category = this.categoryName(this.formData.categoryId);

      // 
      const iconName = this.image.split('?')[0].split('/').slice(-1);
      const params = {
        name: this.formData.name,
        category: this.formData.category,
        categoryId: this.formData.categoryId,
        description: this.formData.description,
        feed: this.formData.feed,
        icon: iconName[0]
      };
      // 
      if (this.$route.query.id) {
        console.log("" + params);
        this.$axios
          .put(`/news/media/${this.$route.query.id}`, params)
          .then(res => {
            if (!res.data) {
              return;
            }
            this.$router.go(-1);
          });
        return;
      }
      // 
      this.$axios.post(`/news/media`, params).then(res => {
        console.log("" + params);
        if (res.status.toString() !== "200") {
          this.$alert(res.message, "", {
            confirmButtonText: ""
          });
          return;
        }
        this.$router.go(-1);
      });
    },
    // 
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    toUpload(){
      let inputFile = document.getElementById('avatar');
      inputFile.click();
    },
    upload(e) {
      this.$axios.get(`/oss/`).then(res => {
        const url = `https://supersuperz.oss-cn-shenzhen.aliyuncs.com`;
        const photo = window.URL.createObjectURL(e.target.files[0]);

        const photoName = e.target.files[0].name;

        const key =
          res.data.dir +
          photoName.split(".")[0] +
          "_" +
          res.data.expire +
          this.random_string(6) +
          "." +
          photoName.split(".")[1];

        const policy = res.data.policy;
        const OSSAccessKeyId = res.data.accessid;
        const callback = res.data.callback;
        const signature = res.data.signature;
        let param = new FormData();

        param.append("key", `${key}`);
        param.append("policy", `${policy}`);
        param.append("OSSAccessKeyId", `${OSSAccessKeyId}`);
        param.append("success_action_status", 200);
        param.append("callback", `${callback}`);
        param.append("signature", `${signature}`);
        param.append("file", e.target.files[0]);

        axios
          .post(url, param)
          .then(response => {
            this.queryImage(key);
            console.log("response == ", response);
          })
          .catch(err => {
            console.log("err == ", err);
          });
      });
    },
    //
    random_string(len) {
      len = len || 32;
      var chars = "ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678";
      var maxPos = chars.length;
      var pwd = "";
      for (let i = 0; i < len; i++) {
        pwd += chars.charAt(Math.floor(Math.random() * maxPos));
      }
      return pwd;
    },
    // 
    queryTypes() {
      this.$axios
        .get("/news/category?pageNum=1&pageSize=40")
        .then(({ data }) => {
          this.typeOptions = data.records.map(el => ({
            ...el,
            label: el.name,
            value: el.categoryId
          }));
        });
    },
    //
    categoryName(id) {
      for (const item of this.typeOptions) {
        if (item.categoryId == id) {
          return item.name;
        }
      }
    },
    // 
    queryImage(url) {
      console.log("" + url);
      let dir = "wuanstar/";
      let key = dir + url.split('?')[0].split('/').slice(-1); //.jpg

      //
      this.$axios.get(`/oss/object?key=${key}`).then(res => {
        console.log("" + key);
        if (res.status.toString() !== "200") {
          console.log("");
          this.image = null;
        }
        this.image = res.data;
        console.log(this.image);
      });
    },
    //media
    queryDetail(id) {
      this.$axios.get(`/news/media/${id}`).then(({ data }) => {
        if (!data) {
          return;
        }
        this.queryImage(data.icon);
        // let iconUrl = this.cacheImage(data.icon)
        this.formData = {
          name: data.name,
          category: data.category,
          categoryId: data.categoryId,
          description: data.description,
          feed: data.feed,
          icon: this.image
        };

        console.log(this.formData);
      });
    }
  }
};
</script>
<style scoped>
.avatar-img{
  margin:10px 0;
}
</style>>